  filter: alpha(opacity=$opacity)
.preloader
  position: fixed
  width: 100%
  height: 100%
  top: 0
  left: 0
  bottom: 0
  top: 0
  z-index: 10000
  background-color: $accent
  .pulse
    position: relative
    left: 50%
    top: 50vh
    margin:
      left: -40px
      right: -40px
    &::before, &::after
      content: ''
      border: 5px solid $white
      width: 80px
      height: 80px
      border-radius: 500px
      position: absolute
    &::before
      animation: pulse-outer .8s ease-in infinite
    &::after
      animation: pulse-inner .8s linear infinite
@keyframes pulse-outer
  0%
    @include opacity(100)
  50%
    @include opacity(50)
  100%
    @include opacity(0)

@keyframes pulse-inner
  0%
    @include opacity(0)
    transform: scale(0)
  100%
    @include opacity(100)
    transform: scale(1)
</style>



