Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.use(Layout)
Vue.use(VueAwesomeSwiper)
Vue.use(VeeValidate)


var VueScrollTo = require('vue-scrollto')

Vue.use(VueScrollTo)


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {App},
})








