<template>
<form @submit.prevent="validateBeforeSubmit">
	<b-container class="alignment">
	<b-row>
	<b-col sm="12" md="6" lg="6" xl="6">
	<!-- <label class="label">Name</label> -->
	<p class="control has-icon has-icon-right">
		<input name="name" v-model="name" v-validate="'required|alpha'" :class="{'input': true, 'is-danger': errors.has('name') }" type="text" placeholder="Your Name">
		<i v-show="errors.has('name')" class="fa fa-warning"></i>
		<span v-show="errors.has('name')" class="help is-danger">{{ errors.first('name') }}</span>
	</p>
	</b-col>
	<b-col sm="12" md="6" lg="6" xl="6">
	<!-- <label class="label">Email</label> -->
	<p class="control has-icon has-icon-right">
		<input name="email" v-model="email" v-validate="'required|email'" :class="{'input': true, 'is-danger': errors.has('email') }" type="text" placeholder="Your Email">
		<i v-show="errors.has('email')" class="fa fa-warning"></i>
		<span v-show="errors.has('email')" class="help is-danger">{{ errors.first('email') }}</span>
	</p>
	</b-col>
	</b-row>
	<b-row>
	<b-col sm="12">
	<!-- <label class="label">Title</label> -->
	<p class="control has-icon has-icon-right">
		<input name="title" v-model="title" v-validate="'required|alpha'" :class="{'input': true, 'is-danger': errors.has('title') }" type="text" placeholder="Your Title">
		<i v-show="errors.has('phone')" class="fa fa-warning"></i>
		<span v-show="errors.has('phone')" class="help is-danger">{{ errors.first('phone') }}</span>
	</p>
	</b-col>
	</b-row>
	<b-row>
	<b-col sm="12">
	
	<!-- <label class="label">Comment</label> -->
	<p class="control has-icon has-icon-right">
		<input name="comment" v-model="comment"  :class="{'input': true, 'is-danger': errors.has('comment') }" type="text" placeholder="Your comment">
		<i v-show="errors.has('comment')" class="fa fa-warning"></i>
		<span v-show="errors.has('comment')" class="help is-danger">{{ errors.first('comment') }}</span>
	</p>
	</b-col>
	</b-row>
	<b-row>
	<b-col sm="12">
	<p class="control">
		<div class="button_wrap">
			<button class="button is-primary" type="submit">Submit</button>
		</div>
	</p>
	</b-col>
	</b-row>
	</b-container>
</form>
</template>
<script>
export default {
  name: 'formexample',
  data: () => ({
    email: '',
    name: '',
    title: '',
    comment: ''
  }),
  methods: {
    validateBeforeSubmit() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          // eslint-disable-next-line
          alert('From Submitted!')
          return
        }

        alert('Correct them errors!')
      })
    }
  }
}

</script>
<style lang="sass">
@import "../styles/vars"

form
	width: 100%
	margin-top: 35px
	margin-bottom: 100px
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	justify-content: center
input::placeholder
	color: #787878
	font-family: $Montserrat
	font-size: 15px
	font-weight: 400
	
input
	width: 100%
	min-height: 50px
	border: none
input[type="text"]
	padding-left: 40px
.button_wrap
	display: flex
	justify-content: center
.button
	color: white
	font-family: $Montserrat
	font-size: 12px
	font-weight: 500
	text-transform: uppercase
	width: 180px
	height: 54px
	background-color: #10c9c3
	margin: auto
	border: none
	transition: all 1s
	&:hover
		cursor: pointer
		transition: all 1s
		transform: scale(1.1)

</style>


