<template>
	<section id="testimonials">
     
      <b-row>
        <b-col>
          <div class="slider_wrapper">
          <!-- The ref attr used to find the swiper instance -->
<!-- The ref attr used to find the swiper instance -->
  <swiper :options="swiperOption" ref="mySwiper" @someSwiperEvent="callback">
    <!-- slides -->
    <swiper-slide>
      <div class="slide_text_wrap1">
        <div class="slide_text">
          <div class="text">“ Outstanding job and exceeded all expectations. It was a pleasure 
          to work with them on a sizable first project and am looking 
          forward to start the next one asap.” 
          </div>
        
            <span>Michael Hopkins</span>
         
        </div>
      </div>
  </swiper-slide>
    <swiper-slide>
       <div class="slide_text_wrap2">
        <div class="slide_text">
          <div class="text">“ Outstanding job and exceeded all expectations. It was a pleasure 
          to work with them on a sizable first project and am looking 
          forward to start the next one asap.” 
          </div>
        
            <span>Michael Hopkins</span>
         
        </div>
      </div>
    </swiper-slide>
    <swiper-slide>
       <div class="slide_text_wrap3">
        <div class="slide_text">
          <div class="text">“ Outstanding job and exceeded all expectations. It was a pleasure 
          to work with them on a sizable first project and am looking 
          forward to start the next one asap.” 
          </div>
        
            <span>Michael Hopkins</span>
         
        </div>
      </div>

    </swiper-slide>
    <!-- Optional controls -->
    <!-- <div class="swiper-pagination_wrap"> -->
    <div class="swiper-pagination swiper-pagination-bullets"  slot="pagination"></div>
    <!-- </div> -->
    
   
  
  </swiper>

    
          </div>
        </b-col>
      </b-row>
    
  </section>
</template>
<script>
 
 export default {
     name: 'carrousel',
     data() {
       return {
         swiperOption: {
           pagination: {
            el: '.swiper-pagination',
             clickable: true,
            
          }
         }
       }
     },
     computed: {
       swiper() {
         return this.$refs.mySwiper.swiper
       }
     },
     mounted() {
       // current swiper instance
       // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
       // console.log('this is current swiper instance object', this.swiper)
       // this.swiper.slideTo(3, 1000, false)
     }
   }
 </script>
 <style lang="sass">
@import "../styles/vars"
#testimonials
  .col
    padding: 0
  .swiper-pagination-bullet-custom
    text-align: center
    line-height: 20px
  .slider_wrapper
    height: 500px
    background-color: #10c9c3
    position: relative
    .swiper-container
      height: 100%
      .swiper-pagination_wrap
        position: absolute
        bottom: 0
        width: 100%
        height: 20%
        .swiper-pagination
          width: 100%
          height: 100%
          margin: auto
  .slide_text_wrap1, .slide_text_wrap2 ,.slide_text_wrap3
    position: absolute
    top: 0
    left: 0
    bottom: 0
    right: 0
    .slide_text
      text-align: center
      margin-top: 100px
      .text, span
        margin: auto
        color: white
        font-family: $Montserrat
        font-weight: 500
      .text
        font-size: 30px
        width: 70%
        margin-bottom: 40px
      span
        font-size: 12px
  .slide_text_wrap1
    background-color: #10c9c3
  .slide_text_wrap2
    background-color: aqua
  .slide_text_wrap3
    background-color: indigo
.swiper-pagination-bullet
  width: 6px
  height: 6px
  background-color: #fff
  margin-right: 10px
.swiper-pagination-bullet-active
  width: 10px
  height: 10px
.swiper-pagination-fraction, .swiper-pagination-custom,.swiper-container-horizontal > .swiper-pagination-bullets
  bottom: 50px
.swiper-slide.swiper-slide-active
  width: 100%!important
 </style>


