<template>
<section id="top">
    <!-- <img src="./assets/img/laptop.jpg"   class="posa" alt=""> -->
    <b-container>
      <b-row>
        <b-col>
          <div class="top_wrap">
            <div class="top">
              <div class="logo"><img src="../assets/img/logo.png" height="25" width="31" alt=""></div>
              <div class="menu_wrap">
              <div class="menu">             
              <nav class="nav_active">
                  <ul>
                    <li><a href="#"  class="a_active">Home</a></li>
                    <li><a href="#" v-scroll-to="'#about_us'">About</a></li>
                    <li><a href="#" v-scroll-to="'#skills'">Skills</a></li>
                    <li><a href="#" v-scroll-to="'#work_process'">Process</a></li>
                    <li><a href="#" v-scroll-to="'#services'">Services</a></li>
                    <li><a href="#" v-scroll-to="'#testimonials'">Testimonials</a></li>
                    <li><a href="#" v-scroll-to="'#footer'">Contact</a></li>
                  </ul>
                </nav>
                  </div>
                  </div> 
                  <div class="menuToggle hamburger hamburger--spin js-hamburger" style="right: 15vw">
              <div class="hamburger-box">
                  <div class="burg hamburger-inner"></div>
              </div>
            </div>          
              </div>
             
            </div>
          </div>

        </b-col>
      </b-row>
      <b-row>
        <b-col> 
          <div class="main_text_wrap">
            <div class="main_text">
              <h1>We Design And Develop</h1>
              <p>We are a new design studio based in USA. We have over
                20 years of combined experience, and know a thing or two
              about designing websites and mobile apps.</p>
              <button>contact us</button>
            </div>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </section>
</template>

<script>
import $ from 'jquery'
require('jquery');
require('jquery-ui-bundle');

export default {
    name: 'mymenu',
    }
 $(function(){
  $('.menu_wrap').on('click', function(){
    if($('.nav_active').css('display') === 'block'){
        $('.nav_active').toggle("slide",{direction: "right"});
      }
      $(".hamburger").toggleClass("dddisplay", function(){
      if($(this).css('opacity') == '1'){
        $(this).removeAttr('style');
      // $('.menuToggle').removeAttr('style');  
      }
    });
      
    
  });
 });
  // var $hamburger = $(".hamburger");
  // $hamburger.on("click", function(e) {
  //   $hamburger.toggleClass("is-activee");
    
  // });
$(function(){
  $('.menuToggle').on('click', function(){
    // alert('asdfsdf');

    $('.nav_active').toggle("slide",{direction: "right"}, function(){
      if($(this).css('display') === 'none'){
        $(this).removeAttr('style');
      // $('.menuToggle').removeAttr('style');  
      }
    });      
        $('.menu_wrapper').toggleClass('menu_wrapper_black');
        $(".hamburger").toggleClass("dddisplay");

        

        // $(".menuToggle").toggleClass("anim");

        });
});


</script>
<!--  -->
<style lang="sass">
@import "../styles/vars"

#top
  min-height: 670px
  position: relative
  background: url(111https://user-images.githubusercontent.com/33417399/34869600-15ab1fe0-f790-11e7-8f09-abc4e8eee4e4.jpg) center center
.posa
  position: absolute
  top: 0
.container
  max-width: 1165px
  // background-color: yellow
.top_wrap
  min-height: 65px
  width: 100%
.top
  display: -webkit-flex
  display: -moz-flex
  display: -ms-flex
  display: -o-flex
  display: flex
  align-items: center
  justify-content: space-between
  min-height: 65px
  width: 100%
.menu
  width: 100%
  display: -webkit-flex
  display: -moz-flex
  display: -ms-flex
  display: -o-flex
  display: flex
  align-items: center
  justify-content: space-between 
.nav_active 
  width: 100%
  display: -webkit-flex
  display: -moz-flex
  display: -ms-flex
  display: -o-flex
  display: flex
  align-items: center
  justify-content: flex-end 
.menu_wrap
  display: -webkit-flex
  display: -moz-flex
  display: -ms-flex
  display: -o-flex
  display: flex
  justify-content: flex-end
  width: 81%
  ul
    width: 75%
    display: -webkit-flex
    display: -moz-flex
    display: -ms-flex
    display: -o-flex
    display: flex
    justify-content: space-between
    float: right
    li
      list-style-type: none
      display: inline-block 
    a, .a_active
        font-family: $Montserrat
        font-size: 12px
        font-weight: 500
        text-transform: uppercase     
    a
      color: $menu   
      padding: 1vh 0
      &:hover
        text-decoration: none
        color: $accent
    .a_active
      color: $accent
.main_text_wrap
  min-height: 670px
  display: -webkit-flex
  display: -moz-flex
  display: -ms-flex
  display: -o-flex
  display: flex
  align-items: center
  justify-content: flex-end
  .main_text
    width: 50%
    h1
      color: #fff
      font-family: $Montserrat
      font-size: 38px
      font-weight: 500
      margin-bottom: 40px
    p
      color: #fff
      font-family: $Montserrat
      font-size: 18px
      font-weight: 400
      margin-bottom: 40px
    button
      width: 180px
      height: 54px
      background-color: $accent
      color: $white
      font-family: $Montserrat
      font-size: 12px
      font-weight: 500
      text-transform: uppercase
      border: none
      transition: all 1s
      &:hover
        cursor: pointer
        transform: scale(1.1)
        transition: all 1s



@media only screen and (max-width : 768px)
  /**/
  .menu_wrap
    width: 100vw
    height: 100vh
    position: absolute
    top: 0
    z-index: 10
  .menuToggle .is-active
    display: block
    display: none
    cursor: pointer
    // border: 1px solid #000
    height: 30px
    width: 30px
    // background-color: transparent
    // position: absolute
    // right: 10%
    // background-color: red
  .dddisplay
    opacity: 0
    transition: all 1s
    .hamactive
      display: none
    .menuToggle  
      display: inline-block
      // position: relative
      // left: -5vh
      position: absolute
      right: 10%
      height: 95px
  #top
    position: relative
  // .menu
  //   display: none
  .container
    padding: 0
  .nav_active
    display: none
    position: absolute
    right: 0
    text-align: center
    z-index: 3
    width: 70vw
    top: 0vh
    height: 100vh
    background-color: #fff
    ul 
      display: block
      width: 100%
      text-align: center
      li
        display: block
        
        background-color: #FFF
        width: 100%
        a 
          display: block
          transition: all 1s
        .a_active
          background-color: $accent
          color: #fff
        &:hover
          background-color: $accent
          cursor: pointer
          a 
            color: #fff
  .hamburger 
      padding: 15px 15px
      display: inline-block
      cursor: pointer
      transition-property: opacity, filter
      transition-duration: 0.3s
      transition-timing-function: linear
      font: inherit
      color: inherit
      text-transform: none
      background-color: transparent
      border: 0
      margin: 0
      overflow: visible 
      z-index: 20
      &
  .hamburger-box 
      width: 40px
      height: 75px
      display: inline-block
      position: relative

  .hamburger-inner 
      display: block
      top: 50%
      margin-top: -2px
      z-index: 3
  .hamburger-inner, .hamburger-inner::before, .hamburger-inner::after 
      width: 40px
      height: 4px
      background-color: #000
      border-radius: 4px
      position: absolute
      transition-property: transform
      transition-duration: 0.15s
      transition-timing-function: ease
  .hamburger-inner::before, .hamburger-inner::after 
      content: ""
      display: block
      
  .hamburger-inner::before 
      top: -10px
  .hamburger-inner::after 
      bottom: -10px
    
      /*
       * Spin
       */
  .hamburger--spin .hamburger-inner 
      transition-duration: 0.22s
      transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); 
      
  .hamburger--spin .hamburger-inner::before 
      transition: top 0.1s 0.25s ease-in, opacity 0.1s ease-in
  .hamburger--spin .hamburger-inner::after 
      transition: bottom 0.1s 0.25s ease-in, transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19)

  .hamburger--spin.is-activee .hamburger-inner 
      transform: rotate(225deg)
      transition-delay: 0.12s
      transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1)
  .hamburger--spin.is-activee .hamburger-inner::before 
      top: 0
      
      transition: top 0.1s ease-out, opacity 0.1s 0.12s ease-out 
      background-color: #FFF
  .hamburger--spin.is-activee .hamburger-inner::after 
      bottom: 0
      transform: rotate(-90deg)
      transition: bottom 0.1s ease-out, transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1)
      background-color: #FFF
</style>


